
.search-filter {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-flow: row;
  align-content: start;
}
.search-filter-input {
  height: 45px;
  width: calc(100% - 45px);
  background: #eee;
  padding: .25em .5em;
  box-sizing: border-box;
}
.search-filter .search-icon-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #cc1f1b;
  width: 45px;
  height: 45px;
}



