
p {
  font-size: 18px;
}
.filter-list {
  flex-direction: column;
}
.filter-list .filter-item {
  width: 100%;
  position: relative;
}
.filter-list .filter-item-content {
  display: flex;
  align-items: center;
  height: 45px;
  padding: .5em .75em .5em .5em;
  cursor: pointer;
  position: relative;
  font-size: .9em;
  margin-top: .25em;
  border-bottom: 2px solid #cecece;
}
.filter-button {
  text-align: left;
}
.filter-option__title {
  position: relative;
}
.toggle-subitems {
  overflow: hidden;
  position: absolute;
  right: 10px;
  top: 50%;
  z-index: 999;
  transform: translateY(-50%);
}



